<template>
  <div class="home">
    <div class="page-title">
      <h1>Cheetah Grid is the ideal grid for enterprises</h1>
    </div>
    <div
      ref="grid"
      class="grid" />
  </div>
</template>
<script>
import {generatePersonsDataSource} from './common'

function createGrid (el) {
  const personsDataSource = (() => {
    const baseDataSource = generatePersonsDataSource(1000000)
    const array = new Array(baseDataSource.length)
    function createMissingData (index) {
      const data = baseDataSource.get(index)
      if (!Math.floor(Math.random() * 10)) {
        data.stars = ''
      }
      if (!Math.floor(Math.random() * 10)) {
        data.progress = ''
      }
      if (!Math.floor(Math.random() * 10)) {
        data.email = ''
      }
      if (!Math.floor(Math.random() * 10)) {
        data.birthday = ''
      }
      return data
    }
    return new cheetahGrid.data.CachedDataSource({
      get (index) {
        return array[index] ? array[index] : (array[index] = createMissingData(index))
      },
      length: baseDataSource.length
    })
  })()

  const grid = new cheetahGrid.ListGrid({
    parentElement: el,
    header: [
      {field: 'check', caption: '', width: 50, columnType: 'check', action: 'check'},
      {field: 'personid', caption: 'ID', width: 85, columnType: 'center'},
      {
        field: 'stars',
        caption: 'Class',
        width: 150,
        columnType: new cheetahGrid.columns.type.IconColumn({
          name: 'star'
        }),
        style: {
          color: 'gold'
        },
        action: new cheetahGrid.columns.action.InlineMenuEditor({options: [
          {value: '', caption: 'Empty'},
          {value: 1, classList: 'stars', html: '<i class="material-icons">star</i>'},
          {value: 2, classList: 'stars', html: '<i class="material-icons">star</i>'.repeat(2)},
          {value: 3, classList: 'stars', html: '<i class="material-icons">star</i>'.repeat(3)},
          {value: 4, classList: 'stars', html: '<i class="material-icons">star</i>'.repeat(4)},
          {value: 5, classList: 'stars', html: '<i class="material-icons">star</i>'.repeat(5)}
        ]}),

        message (rec) {
          return rec.stars ? null : 'Please select.'
        }
      },
      {
        caption: 'Name',
        columns: [
          {
            field: 'fname',
            caption: 'First Name',
            width: '20%',
            minWidth: 150,
            action: new cheetahGrid.columns.action.SmallDialogInputEditor({
              classList: 'helper-text--right-justified',
              helperText (value) {
                return `${value.length}/20`
              },
              inputValidator (value) {
                return value.length > 20 ? `over the max length. ${value.length}` : null
              }
            }),

            message (rec) {
              return rec.fname ? null : 'Please enter First Name.'
            }
          },
          {
            field: 'lname',
            caption: 'Last Name',
            width: '20%',
            minWidth: 150,
            action: new cheetahGrid.columns.action.SmallDialogInputEditor({
              classList: 'helper-text--right-justified',
              helperText (value) {
                return `${value.length}/20`
              },
              inputValidator (value) {
                return value.length > 20 ? `over the max length. ${value.length}` : null
              }
            }),

            message (rec) {
              return rec.lname ? null : 'Please enter Last Name.'
            }

          }
        ]
      },
      {
        field: 'progress',
        caption: 'Progress',
        width: '10%',
        minWidth: 50,
        columnType: new cheetahGrid.columns.type.PercentCompleteBarColumn({
          formatter (v) { return v ? `${v}%` : '' }
        }),
        style: {
          textAlign: 'right',
          padding: [0, 10, 0, 0]
        },
        action: new cheetahGrid.columns.action.SmallDialogInputEditor({
          type: 'number',
          classList: ['al-right'],
          inputValidator (value) {
            return value > 100 ? `over the max value. ${value}` : value < 0 ? `under the min value. ${value}` : null
          }
        }),

        message (rec) {
          return rec.progress > 0 ? null : 'Please enter progress.'
        }
      },
      {
        field: 'email',
        caption: 'Email',
        width: 'calc(50% - 505px - 20px)',
        minWidth: 200,
        action: new cheetahGrid.columns.action.SmallDialogInputEditor({
          helperText (value) {
            return 'Email'
          },
          validator (value) {
            const ret = value.match(/^[-a-z0-9~!$%^&*_=+}{'?]+(\.[-a-z0-9~!$%^&*_=+}{'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$/i)
            return ret ? null : 'Please enter email addr.'
          }
        }),

        message (rec) {
          const {email} = rec
          if (!email) {
            return {
              type: 'error',
              message: 'Please enter Email address.'
            }
          }
          if ((`${rec.fname.replace('-', '_')}_${rec.lname.replace('-', '_')}@example.com`).toLowerCase() !== email) {
            return {
              type: 'warning',
              message: 'Email address is incorrect.'
            }
          }
          return null
        }
      },
      {
        field: {
          get (rec) {
            const d = rec.birthday
            return d ? `${d.getFullYear()}/${d.getMonth() + 1}/${d.getDate()}` : ''
          },
          set (rec, val) {
            rec.birthday = new Date(val)
          }
        },
        caption: 'Birthday',
        width: 100,

        action: new cheetahGrid.columns.action.SmallDialogInputEditor({
          helperText (value) {
            return `birthday`
          },
          validator (value) {
            return isNaN(new Date(value)) ? 'Please enter date.' : null
          }
        }),

        message (rec) {
          return rec.birthday ? null : 'Please enter birthday.'
        }
      },
      {
        caption: '',
        width: 120,
        columnType: new cheetahGrid.columns.type.ButtonColumn({
          caption: 'SHOW REC'
        }),
        action: new cheetahGrid.columns.action.ButtonAction({
          action (rec) {
            alert(JSON.stringify(rec))
          }
        })
      }
    ],
    frozenColCount: 2,
    dataSource: personsDataSource
  })

  grid.listen(cheetahGrid.ListGrid.EVENT_TYPE.CHANGED_VALUE, () => {
    grid.invalidate()
  })

  return grid
}

export default {
  name: 'Enterprise',
  data () {
    return {}
  },
  mounted () {
    if (this.grid) {
      this.grid.dispose()
      this.grid = null
    }
    this.grid = createGrid(this.$refs.grid)
  },
  destroyed () {
    if (this.grid) {
      this.grid.dispose()
      this.grid = null
    }
  }
}
</script>

<style scoped>
  .grid >>> .cheetah-grid__inline-menu__menu-item.stars {
    text-align: center;
    color: gold;
    display: block;
    white-space: nowrap;
  }
  .grid >>> .cheetah-grid__inline-menu__menu-item.stars .material-icons {
    line-height: 40px;
  }
</style>
